import {getRequest} from "./api";

export const initMenu = (router,store) => {
    if (store.state.routes.length>0){
        return;
    }
    getRequest("/system/config/nav").then(data => {
        if (data) {
            let Routes = formatRoutes(data);
            router.addRoutes(Routes);
            store.commit('INIT_Routes',Routes);
        }
    })
};

export const formatRoutes = (routes) => {
    let fmRoutes = [];
    routes.forEach(router => {
        let {
            path,
            component,
            name,
            meta,
            iconCls,
            children
        } = router;
        if (children && children instanceof Array) {
            children = formatRoutes(children);
        }
        let fmRouter = {
            path: path,
            name: name,
            iconCls: iconCls,
            meta: meta,
            children: children,
            component(resolve) {
                if (component.startsWith("Home")) {
                    require(['../views/' + component + '.vue'], resolve);
                } else if (component.startsWith("User")) {
                    require(['../views/user/' + component + '.vue'], resolve);
                } else if (component.startsWith("Movie")) {
                    require(['../views/movie/' + component + '.vue'], resolve);
                } else if (component.startsWith("Book")) {
                    require(['../views/book/' + component + '.vue'], resolve);
                } else if (component.startsWith("Music")) {
                    require(['../views/music/' + component + '.vue'], resolve);
                } else if (component.startsWith("Event")) {
                    require(['../views/event/' + component + '.vue'], resolve);
                }
            }
        };
        fmRoutes.push(fmRouter);
    });
    return fmRoutes;
};