<template>
    <b-container fluid>
        <b-row class="justify-content-md-center">
            <b-col md="auto" class="md-auto p-3 mt-5">
                <b-card header="Welcome" bg-variant="dark" text-variant="white">
                    <b-form @submit="onSubmit" @reset="onReset" v-if="show" ref="logform">
                        <b-form-group id="input-group-1" label="用户名" label-for="input-1">
                            <b-form-input id="input-1" v-model="form.username" type="text" required
                                          placeholder="请输入用户名"></b-form-input>
                        </b-form-group>
                        <b-form-group id="input-group-2" label="密码" label-for="input-2">
                            <b-form-input id="input-2" v-model="form.password" type="password" required
                                          placeholder="请输入密码"></b-form-input>
                        </b-form-group>
                        <b-form-group id="input-group-3">
                            <b-form-checkbox-group v-model="checked" id="checkboxes-3">
                                <b-form-checkbox value="keep">记住密码</b-form-checkbox>
                            </b-form-checkbox-group>
                        </b-form-group>
                        <b-button type="submit" variant="primary">Submit</b-button>
                        <b-button class="ml-2" type="reset" variant="danger">Reset</b-button>
                    </b-form>
                </b-card>
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
    export default {
        name: "Login",
        data() {
            return {
                form: {
                    username: "",
                    password: ""
                },
                checked: [],
                show: true
            }
        },
        methods: {
            onSubmit(event) {
                event.preventDefault();
                this.postKeyValueRequest("/doLogin",this.form).then(resp => {
                    if (resp) {
                        this.$store.commit('INIT_USER',resp.obj);
                        window.sessionStorage.setItem("user",JSON.stringify(resp.obj));
                        let path = this.$route.query.redirect;
                        this.$router.replace((path == '/' || path == undefined) ? '/home' : path);
                    }else{
                        this.$refs.logform.reset();
                    }
                })
            },
            onReset(evt) {
                evt.preventDefault();
                // Reset our form values
                this.form.username = '';
                this.form.password = '';
                this.form.checked = [];
                // Trick to reset/clear native browser form validation state
                this.show = false;
                this.$nextTick(() => {
                    this.show = true
                })
            }
        }
    }
</script>