<template>
    <b-navbar toggleable="lg" type="dark" variant="dark">
        <b-navbar-brand @click="$router.push('/home')" href="#">NightStand</b-navbar-brand>
        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>
        <b-collapse id="nav-collapse" is-nav>
            <b-navbar-nav>
                <b-nav-item-dropdown v-for="(item,index) in routes" :key="index"
                v-show="item.path!='/'&&item.path!='/home'">
                    <template v-slot:button-content>
                        <b-icon :icon="item.iconCls"/>
                        {{item.name}}
                    </template>
                    <b-dropdown-item v-for="(sub,index2) in item.children" :key="index2"
                    @click="$router.push(sub.path)">
                        <b-icon :icon="sub.iconCls"/> {{sub.name}}
                    </b-dropdown-item>
                </b-nav-item-dropdown>
            </b-navbar-nav>

            <!-- Right aligned nav items -->
            <b-navbar-nav class="ml-auto">
                <b-nav-item-dropdown right>
                    <!-- Using 'button-content' slot -->
                    <template v-slot:button-content>
                        {{ user.name==null? '访客':user.name }}
                    </template>
                    <b-dropdown-item v-if="user.name!=null" @click="goProfile">Home</b-dropdown-item>
                    <b-dropdown-item v-if="user.name==null" @click="$router.push('/signUp')">注册</b-dropdown-item>
                    <b-dropdown-item v-if="user.name!=null" @click="logOut">Sign Out</b-dropdown-item>
                </b-nav-item-dropdown>
            </b-navbar-nav>
        </b-collapse>
    </b-navbar>
</template>

<script>
    export default {
        name: "NavBar",
        props: {
            user: {},
            routes: [],
        },
        methods: {
            goProfile() {
                this.$router.push('/userHome');
            },
            logOut() {
                this.$bvModal.msgBoxConfirm("确定要注销吗?", {
                    title: 'Please Confirm',
                    size: 'sm',
                    buttonSize: 'sm',
                    okVariant: 'danger',
                    okTitle: 'YES',
                    cancelTitle: 'NO',
                    footerClass: 'p-2',
                    hideHeaderClose: false,
                    centered: true
                })
                .then(value => {
                    if (value){
                        this.getRequest("/logout");
                        window.sessionStorage.removeItem('user');
                        this.$store.commit("INIT_USER",[]);
                        this.$router.replace('/');
                    }
                });
            }
        }
    }
</script>

