<template>
    <b-container fluid>
        <b-row class="justify-content-md-center">
            <b-col md="auto" class="md-auto p-3 mt-5">
                <b-card header="Welcome" bg-variant="dark" text-variant="white">
                    <b-form @submit="onSubmit" @reset="onReset" v-if="show" ref="logform">
                        <b-form-group id="input-group-1" label="用户名" label-for="input-1">
                            <b-form-input id="input-1" v-model="form.username" type="text" required
                                          placeholder="请输入用户名"></b-form-input>
                        </b-form-group>
                        <b-form-group id="input-group-2" label="email" label-for="input-2">
                            <b-form-input id="input-2" v-model="form.email" type="email" required
                                          placeholder="请输入email"></b-form-input>
                        </b-form-group>
                        <b-form-group id="input-group-3" label="昵称" label-for="input-3">
                            <b-form-input id="input-3" v-model="form.name" type="text" required
                                          placeholder="请输入昵称"></b-form-input>
                        </b-form-group>
                        <b-form-group id="input-group-5" label="简介" label-for="input-5">
                            <b-form-input id="input-5" v-model="form.desc" type="text" required
                                          placeholder="请输入自我介绍"></b-form-input>
                        </b-form-group>
                        <b-form-group id="input-group-4" label="密码" label-for="input-4">
                            <b-form-input id="input-4" v-model="form.password" type="password" required
                                          placeholder="请输入密码"></b-form-input>
                        </b-form-group>
                        <b-button type="submit" variant="primary">Submit</b-button>
                        <b-button class="ml-2" type="reset" variant="danger">Reset</b-button>
                    </b-form>
                </b-card>
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
    export default {
        name: "Login",
        data() {
            return {
                form: {
                    username: "",
                    password: "",
                    name: '',
                    desc: '',
                    email: '',
                },
                show: true
            }
        },
        methods: {
            onSubmit(event) {
                event.preventDefault();
                this.postRequest("/user/sys/",this.form).then(resp => {
                    if (resp.status == 200) {
                        this.$router.push("/")
                    }else{
                        this.$refs.logform.reset();
                    }
                })
            },
            onReset(evt) {
                evt.preventDefault();
                // Reset our form values
                this.form.username = '';
                this.form.password = '';
                this.form.name = '';
                this.form.desc = '';
                this.form.email = '';
                // Trick to reset/clear native browser form validation state
                this.show = false;
                this.$nextTick(() => {
                    this.show = true
                })
            }
        }
    }
</script>